#nav{
	margin-top: 51px;
	margin-right: -40px;
	float: right;
	> ul{
		@include clearfix();
		> li{
			float: left;
			padding: 0;
			margin: 0 40px;
			font-size: .635rem;
			font-weight: 700;
			position: static;
			text-transform: uppercase;
			&.active > a{
				color: #0e6289;
			}
			&:before{
				display: none;
			}
			&:first-child{
				> a:before{
					display: none;
				}
			}
			&:hover > div{
				display: block;
			}
			&:hover > a{
				color: #0e6289;
			}
			> a{
				letter-spacing: 1.1px;
				padding-bottom: 27px;
				display: inline-block;
				position: relative;
				z-index: 9;
				color: $base-color;
				&:before{
					content: "";
					display: inline-block;
					width: 2px;
					height: 12px;
					background-color: #b4b4b6;
					position: absolute;
					left: -40px;
					top: 5px;
					@media (max-width: $lg){
				    	left: -25px;
				  	}
				}
				&:hover,&:focus{
					color: #0e6289;
				}
				@media (max-width: $md){
					padding-bottom: 18px;
				}
			}
			@media (max-width: $lg){
		    	margin: 0 25px;
		  	}
		  	@media (max-width: $md){
		    	font-size: .77rem;
		  	}
		}
	}
	@media (max-width: $lg){
    	margin-right: -15px;
  	}
  	@media (max-width: $md){
		float: none;
		margin: 0 -22px;
		padding-top: 16px;
	}
	@media (max-width: $sm){
    	display: none;
  	}
}
#nav > ul > li > div{
	@include clearfix();
	display: none;
	position: absolute;
	width: 100%;
	left: 0;
	top: 100%;
	background-color: $blue;
	font-size: .91rem;
    text-transform: none;
    color: #e7e7e7;
    font-weight: 400;
    padding: 36px 0 54px 0;
	> ul{
		float: left;
		width: 33.33%;
		padding: 0 0 0 20px;
		&:first-child{
			padding-left: 0;
		}
		> li{
			transition: all .3s ease;
			padding-left: 0;
		    padding-top: 8px;
		    padding-bottom: 7px;
		    margin: 0;
		    &:hover{
		    	background: #07557b;
		    }
		    > a{
		    	display: block;
		    	padding-left: 43px;
		    	@media (max-width: $md){
					padding-left: 20px;
				}
		    }
		    @media (max-width: $md){
				padding-right: 10px;
			}
		}
		> li:before{
			color: #e7e7e7;
			font-size: .73rem;
			left: 30px;
			top: 10px;
			@media (max-width: $md){
				left: 10px;
			}
		}
		@media (max-width: $md){
			padding: 0;
		}
	}
	a{
		color: #e7e7e7;
		&:hover,&:focus{
			font-weight: 400;
			color: #e7e7e7;
		}
	}
}
#nav #magic-line {
    position: absolute;
    bottom: 0px;
    left: 0px;
    width: 0px;
    height: 10px;
    background: $blue;
    z-index: 1;
}
.navigation{
	display: none;
}
@-moz-document url-prefix() { 
  #nav > ul > li > a{
    padding-bottom: 26px;
  }
}
.mm-menu{
	background-color: $blue-sec;
	.mm-listview > li:before{
		display: none;
	}
}
.mm-menu .mm-navbar a, .mm-menu .mm-navbar > *{
	color: $white;
}
.mm-navbar, .mm-listview > li, .mm-listview > li .mm-next, .mm-listview > li .mm-next:before, .mm-listview > li:after,
.mm-menu .mm-listview .mm-btn_next:after, .mm-menu .mm-btn:after, .mm-menu .mm-btn:before{
	border-color: $white;
}
.mm-listview > li > a, .mm-listview > li > span{
	font-weight: 700;
    font-size: 16px;
    padding: 14px 10px 14px 20px;
}
.mm-panel_opened-parent + .mm-panel_has-navbar .mm-listitem:after{
	display: none;
}
.mm-listitem:after {
    left: 0;
}
.mm-listview > li > a, .mm-listview > li > span{
	color: $white !important;
}
.mm-highest .mm-listview > li > a, .mm-highest .mm-listview > li > span{
	text-transform: none;
}
.mm-highest .mm-listview > li:not(.mm-divider):after{
	display: none;
}