/* 16px @ 300px increasing to 22px @ 1280px */
@media (min-width: 300px) {
  :root {
    font-size: calc(1rem + ((1vw - 3px) * 0.6122));
    /* Where: * 0.6122 = 100 * font_Size_Difference / viewport_Width_Difference */
  }
}
/* Prevent font scaling beyond this breakpoint */
@media (min-width: 1280px) {
  :root {
    font-size: 22px;
  }
}