footer{
	.logos{
		padding:20px 15px;
	}
	address{
		padding: 23px 15px;
		text-align: center;
		font-family: $lato;
		text-transform: uppercase;
		color: $grey;
    	letter-spacing: .7px;
    	font-size: 0.635rem;
		a{
			font-weight: 700;
			color: $grey;
			&:hover, &:focus{
				color: $grey;
			}
		}
	}
}
.logos{
	text-align: center;
	background-color: #707070;
	li{
		display: inline-block;
		vertical-align: middle;
		margin: 10px 22px;
		padding: 0;
		&:first-child{
			position: relative;
    		top: -4px;
		}
		&:before{
			display: none;
		}
		@media (max-width: $lg){
    		margin: 10px 16px;
  		}
	}
}
.bottom{
	background-color: $black;
	color: $white;
	font-family: $lato;
	text-align: center;
	.grid-container{
		position: relative;
	}
}
.copyright{
	text-transform: uppercase;
	letter-spacing: 2.2px;
	font-size: 0.5rem;
	padding: 21px 70px;
	a{
		color: $white;
		&:hover, &:focus{
			color: $blue;
		}
	}
	@media (max-width: $md){
		padding: 21px 0;
	}
	@media (max-width: $sm){
		font-size: 0.55rem;
	}
}
.social-icons{
	@include clearfix();
	position: absolute;
	right: 15px;
	padding-top: 15px;
	li{
		float: left;
		width: 30px;
		height: 30px;
		background-color: #eeeeee;
		font-size: 18px;
		padding: 0;
		margin: 0;
		text-align: center;
		transition: all .3s ease;
		&:hover, &:focus{
			background-color: $blue;
			a{
				color: $black;
			}
		}
		&:before{
			display: none;
		}
		a{
			display: block;
			color: $black;
		}
		@media (max-width: $md){
			float: none;
			display: inline-block;
			margin:0 3px;
		}
	}
	li + li{
		margin-left: 5px;
		@media (max-width: $md){
			margin-left: 0;
		}
	}
	@media (max-width: $md){
		position: static;
	    text-align: center;
	    padding-top: 25px;
	}
}
.bottom-text{
	font-size: .545rem;
	font-family: $base-font;
	padding: 0 0 10px;
    margin-top: -1px;
    @media (max-width: $md){
    	padding: 0 0 20px;
  	}	
    @media (max-width: $sm){
		font-size: 0.65rem;
	}
}



