/* VARIABLES
==============================================================================*/
// Typography
$base-font: 'Alegreya Sans', sans-serif;
$vidaloka: 'Vidaloka', serif;
$lato: 'Lato', sans-serif;

// Colors
$base-color: #363636; 
$black: #000000;
$white: #ffffff;
$grey: #464646;
$blue: #13668e;
$blue-sec: #195c7c;
$link: #195c7c;
$link-hover: #000000;
$btn-text: #13668e;
$btn-text-hover: #ffffff;
$btn-bg: #ffffff;
$btn-bg-hover: #195c7c;
$border-color: #195c7c;

// Background colors
$body-bg: #ffffff;


// Font sizes
$base-size: 1rem;
$h1-size: 2.72rem;
$h2-size: 1.82rem;
$h3-size: 1.45rem;
$h4-size: 1.25rem;
$h5-size: 1rem;
$h6-size: .8rem;
$btn-size: 24px;

// media breakpoints
$xs: 479px !default;
$sm: 639px !default;
$lsm: 767px !default;
$md: 1023px !default;
$lg: 1279px !default;
$mlg: 1366px !default;
$xlg: 1440px !default;


// Image Path
$image-path: '../images';

