.slider-hold {
    position: relative;
    height: 100vh;
    .logos{
    	position: absolute;
	    width: 100%;
	    bottom: 50px;
	    background-color: transparent;
	    li{
	    	margin: 10px 20px;
	    	img{
	    		@media (max-width: $md){
					width: 80px;
				}
	    	}
	    	@media (max-width: $lg){
				margin: 10px 12px;
			}
	    }
	    @media (max-width: $lsm){
			position: relative;
			bottom: 0;
			background: #707070;
			padding: 20px 15px;
		}
    }
    @media (max-width: $lsm){
		height: auto;
	}
}
.home-slider {
    height: 100%;
    .slick-list{
    	width: 100%;
    	height: 100%;
    }
    .slide-thumb{
    	position: absolute;
	    width: 100vw;
	    height: 100vh;
	    background-repeat: no-repeat;
	    background-position: top;
	    background-size: cover;
	    @media (max-width: $lsm){
			display: none;
		}
    }
    .slide-mobile{
    	display: none;
    	img{
    		@media (max-width: $lsm){
				width: 100%;
			}
    	}
    	@media (max-width: $lsm){
			display: block;
			padding-top: 113px;
		}
		@media (max-width: $sm){
			padding-top: 60px;
		}
    }
    .outer{
    	position: relative;
	    display: table;
	    width: 100vw;
	    height: 100vh;
	    @media (max-width: $lsm){
			display: block;
			width: 100%;
			height: auto;
		}
    }
    .slide-content{
    	display: table-cell;
	    width: 100%;
	    padding-top: 220px;
	    vertical-align: top;
	    .grid-container{
	    	@media (max-width: $lsm){
				padding: 0;
			}
	    }
	    @media (max-width: $xlg){
			padding-top: 180px;
		}
		@media screen and (max-width: $mlg) and (max-height: $lsm) {
			padding-top: 170px;
		}
		@media (max-width: $lsm){
			display: block;
			width: 100%;
			padding-top:0;
		}
    }
    @media (max-width: $lsm){
		height: auto;
	}
}
.msg-box{
	padding: 0 0 0 40px;
    max-width: 730px;
    position: relative;
    color: $white;
    h2{
    	background: $black;
	    color: $white;
	    font-size: 2.54rem;
	    padding: 0 79px 0 12px;
	    margin: 0;
	    display: inline-block;
	    @media (max-width: $xlg){
			font-size: 2rem;
		}
		@media screen and (max-width: $mlg) and (max-height: $lsm) {
			font-size: 1.5rem;
		}
		@media (max-width: $lsm){
			display: block;
		    font-size: 2rem;
		    padding: 20px 15px;
		}
    }
    h1{
    	display: inline-block;
	    background-color: #1e6080;
	    background-image: linear-gradient(#1e6080,#1a5d7d);
	    font-size: 3.64rem;
	    line-height: .8;
	    color: $white;
	    padding: 0 25px 10px 12px;
	    br{
	    	@media (max-width: $lsm){
				display: none;
			}
	    }
	    margin-bottom: 17px;
	    @media (max-width: $xlg){
			font-size: 2.6rem;
		}
		@media screen and (max-width: $mlg) and (max-height: $lsm) {
			font-size: 2.2rem;
			margin-top: -2px;
			margin-bottom: 10px;
		}
		@media (max-width: $lsm){
			margin: 0;
		    display: block;
		    font-size: 2.6rem;
		    padding: 15px 15px;
		    line-height: 1;
		}
    }
    h3{
    	font-size: 1.82rem;
    	color: $white;
    	line-height: 1.3;
    	position: relative;
    	padding-bottom: 20px;
    	margin: 0 0 16px;
    	&:after{
    		content: "";
    		display: inline-block;
    		width: 60px;
    		height: 4px;
    		background-color: $white;
    		position: absolute;
    		bottom: 0;
    		left: 0;
    		@media (max-width: $lsm){
				left: 15px;
			}
    	}
    	br{
    		@media (max-width: $lsm){
				display: none;
			}
    	}
    	@media (max-width: $xlg){
			font-size: 1.5rem;
		}
		@media screen and (max-width: $mlg) and (max-height: $lsm) {
			font-size: 1.2rem;
		}
		@media (max-width: $lsm){
			font-size: 1.5rem;
    		padding: 10px 15px 20px 15px;
		}
    }
    p{
    	font-size: .91rem;
    	@media (max-width: $xlg){
			font-size: .81rem;
		}
		@media screen and (max-width: $mlg) and (max-height: $lsm) {
			font-size: .71rem;
		}
		@media (max-width: $lsm){
			font-size: .91rem;
		    margin: 0;
		    padding: 0 15px 10px 15px;
		}
    }
    .box{
    	max-width: 550px;
    	@media (max-width: $lsm){
    		padding: 20px 0;
			max-width: 100%;
			background-color: $black;
		}
    }
    @media (max-width: $lsm){
    	max-width: 100%;
		padding: 0;
	}
}
.white-line{
	@include background-image(white-line,png,left top);
	background-size: cover;
	max-width: 741px;
	height: 91px;
	position: absolute;
    top: -49px;
    left: 220px;
    width: 0;
    transition: width 2s ease-in-out;
    &.line-active{
    	width: 100%;
    }
    @media (max-width: $xlg){
		left: 182px;
	}
	@media (max-width: $lsm){
		display: none;
	}
}
.scroll{
	position: absolute;
	z-index: 9;
    width: 150px;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
    background: #1c5f7f;
    color: $white;
    text-transform: uppercase;
    font-size: 13px;
    font-weight: 800;
    letter-spacing: 1px;
    padding: 14px 10px;
    &:hover{
    	color: #1c5f7f;
    	background: $white;
    	&:after{
    		background: url("../images/white-arrow.png") no-repeat center center;
    	}
    }
    &:focus{
    	background: #1c5f7f;
    	color: $white;
    	&:after{
    		background: url("../images/blue-arrow.png") no-repeat center center;
    	}
    }
    &:after{
    	content: "";
    	display: inline-block;
    	width: 28px;
    	height: 14px;
    	background: url("../images/blue-arrow.png") no-repeat center center;
    	position: absolute;
    	top:100%;
    	left: 50%;
    	transform: translateX(-50%);
    }
    @media (max-width: $lsm){
		display: none;
	}
}
.home-main{
	h2{
		text-transform: uppercase;
    	font-size: 2.18rem;
    	text-align: center;
	}
}
#about{
	background-repeat: no-repeat;
	background-position: center top;
	background-size: cover;
	text-align: center;
	padding: 120px 0 108px 0;
	.text{
		max-width: 955px;
    	margin: 0 auto;
	}
	h2{
		margin-bottom: 58px;
		@media (max-width: $sm){
			margin-bottom: 38px;
		}
	}
	p{
		margin-bottom: 108px;
		@media (max-width: $sm){
			margin-bottom: 58px;
		}
	}
	@media (max-width: $sm){
		padding: 90px 0;
	}
}
.news{
	padding: 104px 0 112px 0;
	background-color: #000;
    background-image: linear-gradient(#101010, #141414);
	h2{
		color: $white;
		margin-bottom: 30px;
	}
	.grid-x{
		margin: 0 -15px;
	}
	.post{
		border-bottom: none;
		h3{
			color: #2a8fbf;
			font-size: 1.365rem;
			margin-bottom: 27px;
			a{
				color: #2a8fbf;
			}
			@media (max-width: $lg){
				height: auto !important;
    		}
		}
		.date{
			color: #707070;
			font-size: 0.635rem;
		    text-transform: uppercase;
		    font-weight: 900;
		    line-height: 1;
		    padding-top: 20px;
		    border-top: 1px solid #363636;
		    letter-spacing: .9px;
		    a{
		    	color: #707070;
		    	display: inline-block;
		    	padding: 0 0 0 20px;
		    	position: relative;
		    	&:hover, &:focus{
					color: $white;
				}
		    	&:before{
		    		content: "\f017";
				    font-family: FontAwesome;
				    font-weight: 400;
				    position: absolute;
				    left: 0;
				    top: 0;
		    	}
		    }
		}
	}
	@media (max-width: $sm){
		padding: 90px 0;
	}
}
.news-slider{
	margin-top: 90px;
	.slick-list{
		margin-bottom: 87px;
		@media (max-width: $md){
    		margin-bottom: 25px;
  		}
  		@media (max-width: $sm){
			margin-bottom: 0;
		}
	}
	.slick-dots{
		text-align: center;
		li{
			display: inline-block;
		    margin: 0 5px;
		    padding: 0;
		    width: 13px;
		    height: 13px;
		    border: 2px solid $white;
		    border-radius: 50%;
		    cursor: pointer;
		    font-size: 0;
		    transition: all .3s ease;
			&:before{
				display: none;
			}
			&:hover, &:focus, &.slick-active{
				background-color: $blue;
				border-color: $blue;
			}
		}
	}
	@media (max-width: $sm){
		margin-top: 60px;
	}
}
.pa{
	padding-top: 120px;
	padding-bottom: 82px;
	h2{
		margin-bottom: 47px;
	}
	@media (max-width: $sm){
		padding-top: 90px;
		padding-bottom: 40px;
	}
}
.pa-list{
	@include clearfix();
	padding: 0 153px;
	margin: 0 -5px;
	> figure{
		float: left;
		cursor: pointer;
		width: calc(50% - 10px);
		margin: 0 5px 10px 5px;
		overflow: hidden;
		position: relative;
		text-align: center;
		&:hover img{
			transform: scale(1);
		}
		img{
			position: relative;
		    display: block;
		    min-height: 100%;
		    max-width: 100%;
		    width: 100%;
		    transition: transform 0.35s;
		    transform: scale(1.12);
		}
		figcaption{
			padding: 1em;
			position: absolute;
		    top: 0;
		    left: 0;
		    width: 100%;
		    height: 100%;
		    z-index: 9;
			> a{
				z-index: 1000;
				text-indent: 200%;
			    white-space: nowrap;
			    font-size: 0;
			    opacity: 0;
			    position: absolute;
			    top: 0;
			    left: 0;
			    width: 100%;
			    height: 100%;
			}
			> div{
				position: absolute;
			    width: 100%;
			    left: 0;
			    padding: 0 15px;
			    top: 50%;
			    transform: translateY(-50%);
			    padding-top: 35px;
			}
			h3{
				font-weight: 700;
			    color: $white;
			    text-transform: uppercase;
			    margin: 0;
			    font-size: .82rem;
			    letter-spacing: 3.4px;
			    font-family: $base-font;
			    @media (max-width: $md){
					font-size: .65rem;
				}
				@media (max-width: $sm){
					font-size: .82rem;
				}
			}
			.more{
				font-size: .8rem;
				color: $white;
				position: relative;
				&:after{
					content: "\f178";
					font-family: FontAwesome;
					font-size: .7rem;
    				margin: 0 0 0 8px;
				}
				@media (max-width: $md){
					top: -4px;
				}
			}
		}
		@media (max-width: $sm){
			width: calc(100% - 10px);
		}
	}
	figure:before{
		content: "";
		display: block;
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		background-color: rgba(25, 92, 124, 0.6);
    	z-index: 9;
    	opacity: 0;
    	transition: opacity 0.35s, background-color 0.35s;
	}
	figure:hover:before{
		opacity: 1;
	}
	figure .more, figure figcaption:before{
		transform: scale(1.5);
		opacity: 0;
		transition: opacity 0.35s, transform 0.35s;
	}
	figure figcaption:before{
		position: absolute;
	    top: 15px;
	    right: 15px;
	    bottom: 15px;
	    left: 15px;
	    border: 1px solid $white;
	    content: '';
	    transform: scale(1.1);
	}
	figure:hover figcaption::before, figure:hover .more{
		opacity: 1;
    	transform: scale(1);
	}
	@media (max-width: $lg){
		padding: 0 50px;
    }
    @media (max-width: $md){
		padding: 0;
	}
}
.animated {
    opacity: 1 !important;
}
.homepage header, .slide-thumb , .white-line, .slide-content .msg-box h2, .slide-content .msg-box h1,
.slide-content .msg-box .box, .slider-hold .logos, .slider-hold .scroll, .home-main > *, .homepage footer{
	opacity: 0;
}
@media (max-width: $lsm){
 	.homepage header, .slide-thumb , .white-line, .slide-content .msg-box h2, .slide-content .msg-box h1,
	.slide-content .msg-box .box, .slider-hold .logos, .slider-hold .scroll, .home-main > *, .homepage footer{
    	opacity: 1 !important;
  	}
}




