.top-spacer{
	height: 100px;
	@media (max-width: $md){
		height: 109px;
	}
	@media (max-width: $sm){
    	height: 60px;
  	}
}
.banner{
	height: 400px;
	background-repeat: no-repeat;
	background-position: center top;
	background-size: cover;
	display: table;
    width: 100%;
    text-align: center;
    > div{
    	padding: 0 15px;
    	display: table-cell;
    	vertical-align: middle;
    }
    h1{
    	max-width: 752px;
    	padding: 36px 0;
    	margin: 0 auto;
    	background-color: rgba(19, 102, 142, 0.8);
    	@media (max-width: $sm){
    		padding: 16px 0;
  		}
    }
}
#main{
	padding-top: 30px;
	padding-bottom: 30px;
}
.sidebar{
	margin: 0 0 10px;
	padding-top: 50px;
	.section{
		margin-bottom: 48px;
		@media (max-width: $sm){
			margin-bottom: 30px;
		}
	}
	.section.pages{
		padding: 23px 30px 23px 30px;
		border: 1px solid #dfdfdf;
		h3{
			margin-left: 10px;
			@media (max-width: $lg){
    			margin-left: 0;
  			}
		}
		ul{
			li a{
				color: $base-color;
			}
			li a:hover, li a:focus{
				color: $blue-sec;
				font-weight: 400;
				text-shadow: -1px 0 0 $blue-sec;
			}
			@media (max-width: $sm){
    			display: none;
  			}
		}
		@media (max-width: $sm){
			padding: 23px 15px 23px 15px;
		}
	}
}
.search-box{
	position: relative;
	border: 3px solid $border-color;
	input{
		@include placeholder($base-color,1);
		margin: 0;
		box-shadow: none;
	    border: none;
	    height: 54px;
	    padding: 10px 108px 10px 20px;
	    font-size: .725rem;
	    color: $base-color;
		&:focus{
			box-shadow: none;
			border: none;
		}
		@media (max-width: $sm){
			font-size: 1rem;
		}
		@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {  
			padding: 13px 108px 10px 20px;
		}
	}
	.submit{
		position: absolute;
	    right: 0;
	    top: 0;
	    text-transform: uppercase;
	    font-size: .725rem;
	    font-weight: 800;
	    color: $blue;
	    width: 104px;
    	border-left: 3px solid $border-color;
    	height: 54px;
    	cursor: pointer;
    	transition: all .3s ease;
    	&:hover{
    		color: $white;
    		background-color: $btn-bg-hover;
    	}
    	@media (max-width: $sm){
			font-size: 1rem;
		}
		@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {  
			padding-top: 4px;
		}
	}
}
.content{
	padding-top: 42px;
	@media (max-width: $sm){
		padding-top: 22px;
	}
}
.post{
	padding-bottom: 40px;
	border-bottom: 1px solid #b7b7b7;
	.info{
		font-size: 0.635rem;
	    text-transform: uppercase;
	    font-weight: 900;
	    color: #b7b7b7;
	    margin-bottom: 10px;
	    letter-spacing: .9px;
	    a{
	    	color: $blue-sec;
	    	&:hover,&:focus{
	    		color: #707070;
	    	}
	    }
	    span{
	    	position: relative;
	    	padding:0 0 0 22px;
	    	margin-bottom: 10px;
	    	color: #707070;
	    	display: inline-block;
	    	@media (max-width: $sm){
				padding: 0 0 0 15px;
			}
	    }
	    .date:before{
	    	content: "\f017";
	    	font-family: FontAwesome;
	    	font-weight: 400;
	    	position: absolute;
		    left: 0;
		    top: 1px;
		    @media (max-width: $sm){
    			top: 0;
  			}
	    }
	    .author:before{
	    	content: "\f2c0";
	    	font-family: FontAwesome;
	    	font-weight: 400;
	    	position: absolute;
		    left: 0;
		    top: 1px;
		    @media (max-width: $sm){
    			top: 0;
  			}
	    }
	    .cat:before{
	    	content: "\f02b";
	    	font-family: FontAwesome;
	    	font-weight: 400;
	    	position: absolute;
		    left: 0;
		    top: 1px;
		    @media (max-width: $sm){
    			top: 0;
  			}
	    }
	}
	.entry{
		h3{
			margin: 0 0 3px;
		}
		img{
			margin: 10px 31px 67px 0;
		}
		img.left{
			@media (max-width: $sm){
				float: none;
				display: block;
				margin: 10px 0 20px;
			}
		}
	}
	.entry-thumb{
		margin-top: 9px;
	}
}
.post + .post{
	margin-top: 69px;
	@media (max-width: $sm){
		margin-top: 49px;
	}
}
.post-head{
	margin-bottom: 31px;
	border-bottom: 1px solid #b7b7b7;
	h2 a{
		color: $black;
	}
}
.spacer{
	padding-right: 108px;
	@media (max-width: $lg){
    	padding-right: 50px;
  	}
  	@media (max-width: $md){
    	padding-right: 0;
  	}
}
.more-options{
	@include clearfix();
	clear: both;
	padding-top: 3px;
}
.other-links{
	margin-top: 8px;
	float: right;
	color: #959595;
	font-size: .635rem;
    font-weight: 900;
    text-transform: uppercase;
    letter-spacing: .9px;
    a{
    	font-weight: 700;
    	position: relative;
    	display: inline-block;
    	&:hover, &:focus{
    		color: #959595;
    	}
    }
    .comments{
    	padding:0 0 0 25px;
    	&:before{
    		content: "\f075";
	    	font-family: FontAwesome;
	    	color: #959595;
	    	position: absolute;
	    	left: 0;
	    	top: 1px;
	    	@media (max-width: $xs){
    			top: 0px;
  			}
  			@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {  
    			top: 1px;
  			}
    	}
    }
    .share{
    	padding-right: 20px;
    	&:after{
    		content: "\f1e0";
    		font-family: FontAwesome;
	    	color: #959595;
	    	position: absolute;
	    	top: 0px;
	    	right: 0;
	    	@media (max-width: $xs){
    			top: 0px;
  			}
  			@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {  
    			top: 1px;
  			}
    	}
    }
    @media (max-width: $sm){
		margin-top: 13px;
	}
    @media (max-width: $xs){
		float: none;
	}
}
.wp-pagination{
	@include clearfix();
	margin: 68px 0 56px 0;
	.arrow{
		font-size: .91rem;
		text-transform: uppercase;
		color: $blue;
		font-weight: 900;
		cursor: pointer;
		margin-top: 5px;
		transition: all .3s ease;
		&:hover{
			color: $base-color;
		}
	}
	.next.arrow{
		margin-left: 23px;
	}
	.next.arrow .fa{
		margin: 0 0 0 8px;
	}
	.prev.arrow{
		margin-right: 23px;
	}
	.prev.arrow .fa{
		margin: 0 8px 0 0;
	}
	> *{
		float: left;
	}
	@media (max-width: $sm){
		margin: 48px 0 0 0;
	}
}
.wp-pagenavi{
	@include clearfix();
	li{
		margin: 0;
		font-weight: 900;
	    color: #464646;
	    float: left;
	    padding: 0;
	    line-height: 1.5;
	    min-width: 30px;
	    text-align: center;
		&:before{
			display: none;
		}
	}
	li + li{
		margin-left: 8px;
	}
	.current{
		color: $white;
		display: block;
		padding: 0 10px;
		background-color: $blue;
	}
	a{
	    color: #464646;
	    display: block;
	    padding: 0 10px;
	    &:hover, &:focus{
	    	color: $white;
	    	background-color: $blue;
	    }
	}
}

