header{
	width: 100%;
    left: 0;
    top: 0;
    position: fixed;
    z-index: 99;
	background-color: #e7e7e7;
	.grid-container{
		@media (max-width: $md){
    		padding-left: 0;
    		padding-right: 0;
    	}
	}
}
.header-wrap{
	position: relative;
	> .grid-x{
		margin:0 -15px;
	}
}
.logo{
	padding: 25px 0 26px 0;
    display: inline-block;
    img{
    	@media (max-width: $md){
    		width: 229px;
    	}
    }
    @media (max-width: $md){
    	display: block;
    	border-bottom: 1px solid #b7b7b7;
	    padding: 12px 10px 9px 10px;
    }
    @media (max-width: $sm){
		display: inline-block;
    	padding: 12px 10px 13px 10px;
    	border-bottom: none;
	}
}
.mobile-menu{
	display: none;
	float: right;
    font-size: 20px;
    width: 60px;
    height: 60px;
    text-align: center;
    border-radius: 1px;
    background: $blue-sec;
    color: $white;
    padding-top: 15px;
    &:hover, &:focus{
    	color: $white;
    }
    @media (max-width: $sm){
		display: inline-block;
	}
}
#menu{
	@media (min-width: $sm + 1){
		display: none;
	}
}