body {
  line-height: 1.65;
  font-family: $base-font;
  color: $base-color;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  position: relative;
  overflow-x: hidden;
}
img{
  max-width: 100%;
}
a{
  outline: 0;
  transition: all .3s ease-out;
  text-decoration: none;
  color: $link;
  &:hover,&:focus{
    outline: 0;
    outline-offset: 0;
    text-decoration: none;
    color: $link-hover;
  }
}
.figure img{
  width: 100%;
  display: block;
}
p{
  line-height: 1.65;
  font-size: $base-size;
  margin: 0 0 36px;
  @media (max-width: $sm){
    margin: 0 0 20px;
  }
}
ol{
  margin:0;
  padding:0;
  font-size: .909rem;
}
.left{
  float: left;
}
.right{
  float: right;
}
.center{
  text-align: center;
}
h1,h2,h3,h4,h5,h6{
  font-family: $vidaloka;
  font-weight: 400;
}
h1{
  font-size: $h1-size;
  margin:0;
  line-height: 1.2;
  color: $white;
}
h2{
  font-size: $h2-size;
  margin: 0 0 11px;
  line-height: 1.2;
  position: relative;
  color: $black;
}
h3{
  font-size: $h3-size;
  margin:0 0 19px;
  line-height: 1.2;
  color: $black;
}
h4{
  font-size: $h4-size;
  margin: 0 0 15px;
  line-height: 1.2;
  color: $black;
}
h5{
  font-size: $h5-size;
  margin: 0 0 15px;
  line-height: 1.2;
  color: $black;
}
h6{
  font-size: $h6-size;
  margin: 0 0 15px;
  line-height: 1.2;
  color: $black;
}
nav ul, nav ol {
  list-style: none;
  list-style-image: none;
  margin: 0;
  padding: 0;
}
ul{
  list-style: none;
  padding:0;
  margin: 0;
  font-size: .909rem;
  li{
    position: relative;
    padding: 0 0 0 14px;
    margin-bottom: 17px;
    &:before{
      font-family: FontAwesome;
      position: absolute;
      left: 0;
      padding: 0;
      content: '\f0da';
      color: $blue-sec;
    }
  }
  li a{
    &:hover, &:focus{
      font-weight: 700;
      color: #1d5f7e;
    }
  }
  @media (max-width: $sm){
    
  }
}
blockquote{
  font-size: 26px;
  font-weight: 700;
  font-style: italic;
  line-height: 1.4;
}
*:focus {
  outline: 0;
}
:after, :before {
  box-sizing: border-box;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
}
input:focus, textarea:focus, button:focus, select:focus, div:focus, .form-control:focus {
  outline: none;
  background-color: transparent;
  border-radius: 0;
}
address{
  font-style: normal;
}
.grid-container {
  max-width: 1280px;
  padding-right: 15px;
  padding-left: 15px;
}
.grid-container:not(.full) > .grid-padding-x{
  margin-right: -15px;
  margin-left: -15px;
}
.grid-padding-x > .cell {
  padding-right: 15px;
  padding-left: 15px;
}
.btn{
  min-width: 160px;
  text-align: center;
  display: inline-block;
  border: 3px solid $border-color;
  font-size: .635rem;
  font-weight: 900;
  text-transform: uppercase;
  color: $btn-text;
  transition: all .3s ease;
  letter-spacing: .8px;
  padding: 10px 10px 11px 10px;
  &:hover, &:focus{
    background-color: $btn-bg-hover;
    color: $btn-text-hover;
  }
  @media (max-width: $sm){
    font-size: .73rem;
  }
  @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {  
    padding: 11px 10px 10px 10px;
  }
}
.btn-large{
  min-width: 210px;
  padding: 15px 10px 16px 10px;
  @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {  
    padding: 17px 10px 14px 10px;
  }
}




